.create-board {

    .twitter-picker {
        margin-top: 12px;
    }

    .image-select {
        width: 276px;
        list-style: none;
        padding: 0;
        margin: 0;
        margin-top: 10px;

        display: flex;
        justify-content: space-around;
        box-shadow: rgb(0 0 0 / 25%) 0px 1px 4px;
        border-radius: 4px;
        border: 0px solid rgba(0, 0, 0, 0.25);
        height: 40px;
        align-items: center;

        li {

            height: 30px;
            width: 30px;
        }

        button {
            border-radius: 4px;
            height: 30px;
            width: 30px;
        }

    }

    form {
        margin-top: 15px;

        input {
            width: 100%;
            outline: none;
            border: none;
            color: $clr7;
            margin-bottom: 15px;
            border-radius: 3px;
            box-shadow: inset 0 0 0 2px #dfe1e6;
            background-color: #fafbfc;
            padding: 8px 12px;

        }
    }

    #createbtn {
        background-color: $clr3;
        color: inherit;
        width: 100%;
        border-radius: 3px;
        margin-bottom: 10px;
        background-color: rgba(9, 30, 66, 0.04);
        transition: background-color 0.2s;

        &:hover {
            background-color: rgba(9, 30, 66, 0.122);
        }
    }
}

.create-board-header {
    margin-bottom: 8px;
    padding: 0 12px;
    position: relative;
    text-align: center;
    display: grid;
    align-items: center;
    grid-template-columns: 12px 1fr 12px;

    i {
        grid-column: 3;
        grid-row: 1;
        background: transparent;
        border: none;
        cursor: pointer;
        margin: 0;
        padding: 0;
        z-index: 2;
        outline: none;
    }

    h4 {
        grid-column: 1 / span 3;
        grid-row: 1;
        border-bottom: 1px solid rgba(9, 30, 66, 0.13);
        color: $clr15;
        height: 40px;
        display: block;
        line-height: 40px;
        margin: 0;
        overflow: hidden;
        padding: 0 32px;
        position: relative;
        text-overflow: ellipsis;
        white-space: nowrap;
    }


}


.create-board-preview {
    display: flex;
    justify-content: center;
    padding-bottom: 15px;
    flex-grow: 1;

    .background-preview {
        width: 200px;
        height: 110px;
        background: none;
        background-position: center center;
        background-size: cover;
        border-radius: 3px;
        box-shadow: 0 7px 15px rgba(0, 0, 0, 0.15);
        display: flex;
        align-items: center;
        justify-content: center;

        img {
            padding: 30px 15px;
        }
    }


}