.members-action-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  .members-action-info {
    display: flex;
    li {
      cursor: pointer;
      display: flex;
      align-items: center;
      gap: 0.2em;
      width: 100%;
    }
  }
  .members-action-detail {
    margin-right: 1em;
  }
}
